.header__container {
  height: 220px;
  width: 100wh;
  display: flex;
  justify-content: center;
  padding: 30px;
}
.header {
  display: flex;
  flex-direction: row;
  justify-content:space-between;
  align-items: center;
  height: 180px;
  width: 100%;
  max-width: 800px;
  border: solid rgba(94,111,137,255) 2px;
  border-radius: 10px;
  color: white;
}

.header-left {
  margin-left: 35px;
  text-align: start;
  line-height:18px;
}

.header-right {
  margin-right: 35px;
  background-color: #fbfbfb;
  color:#514f63;
  padding: 15px 35px 5px;
  border-radius: 15px;
}

.header-right-score {
  font-size: 60px;
}

img {
  width: 20px;
  height: 20px;
}

@media (max-width: 1280px) {
  /* Estilos que se aplicarán cuando el ancho de la pantalla sea menor a 1200px */
  .header__container {
    height: 160px;
    width: 100wh;
    display: flex;
    justify-content: center;
    padding: 30px;
  }

  .header {
    height: 100%;
    width: 100%;
  }
  .header-right {
    margin-right: 35px;
    background-color: #fbfbfb;
    color:#514f63;
    padding: 15px 35px 5px;
    border-radius: 15px;
    height: 80%;
  }

  .header-right-score {
    font-size: 30px;
  }
}